<template>
    <section>
        <el-row :gutter="10" style="margin-left: 0; margin-right: 0">
            <el-col :span="18">
                <el-form
                    :model="addForm"
                    label-width="120px"
                    :rules="addFormRules"
                    ref="addForm"
                >
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="监测对象名称" prop="sitename">
                                <el-input
                                    maxlength="50"
                                    placeholder="请输入监测对象名称"
                                    v-model="addForm.sitename"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item
                                label="监测对象简称"
                                prop="siteshortname"
                            >
                                <el-input
                                    placeholder="请输入监测对象简称"
                                    maxlength="12"
                                    show-word-limit
                                    v-model="addForm.siteshortname"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="监测类别" prop="sitetype">
                                <el-select
                                    v-model="addForm.sitetype"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in sitetypes"
                                        :key="item.value"
                                        :label="item.text"
                                        :value="item.value"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="所属分组" prop="GROUPID">
                                <el-select
                                    v-model="addForm.GROUPID"
                                    filterable
                                    placeholder="请选择"
                                    @change="changeCity"
                                >
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="所属区" prop="sitecity">
                                <el-select
                                    disabled
                                    v-model="addForm.sitecity"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in citys"
                                        :key="item.RegionId"
                                        :label="item.RegionName"
                                        :value="item.RegionId"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item prop="sitearea">
                                <el-select
                                    v-model="addForm.sitearea"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in districts"
                                        :key="item.RegionId"
                                        :label="item.RegionName"
                                        :value="item.RegionId"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="监测对象地址" prop="siteaddr">
                                <el-input
                                    v-model="addForm.siteaddr"
                                    auto-complete="off"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="经纬度" prop="sitelng">
                                <el-row type="flex" justify="space-between">
                                    <el-col :span="11"
                                        ><el-input
                                            v-model="addForm.sitelng"
                                            auto-complete="off"
                                            type="tel"
                                        ></el-input
                                    ></el-col>
                                    <el-col :span="11">
                                        <el-input
                                            v-model="addForm.sitelat"
                                            auto-complete="off"
                                            type="tel"
                                        ></el-input
                                    ></el-col>
                                </el-row>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label="项目编号" prop="sitecode">
                                <el-input
                                    v-model="addForm.sitecode"
                                    auto-complete="off"
                                    type="tel"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8"
                            ><el-form-item label="开始日期" prop="startdate">
                                <el-date-picker
                                    v-model="addForm.startdate"
                                    type="date"
                                    :picker-options="pickerOptionsStart"
                                    placeholder="选择开始日期"
                                >
                                </el-date-picker
                            ></el-form-item>
                        </el-col>
                        <el-col :span="8"
                            ><el-form-item label="结束日期" prop="enddate">
                                <el-date-picker
                                    v-model="addForm.enddate"
                                    type="date"
                                    :picker-options="pickerOptionsEnd"
                                    placeholder="选择结束日期"
                                >
                                </el-date-picker
                            ></el-form-item>
                        </el-col>
                        <el-col :span="8"
                            ><el-form-item
                                label="服务供应商"
                                prop="serviceprovider"
                            >
                                <el-select
                                    v-model="addForm.serviceprovider"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in mprovider"
                                        :key="item"
                                        :label="mprovider[item]"
                                        :value="item"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="项目经理" prop="contact">
                                <el-input
                                    maxlength="50"
                                    v-model="addForm.contact"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8"
                            ><el-form-item label="项目经理电话" prop="tel">
                                <el-input
                                    maxlength="50"
                                    v-model="addForm.tel"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                        <el-col :span="8"
                            ><el-form-item
                                label="项目经理身份证"
                                prop="contactidcard"
                            >
                                <el-input
                                    maxlength="50"
                                    v-model="addForm.contactidcard"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="建设单位" prop="constructor1"
                                ><el-input
                                    maxlength="50"
                                    v-model="addForm.constructor1"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8"
                            ><el-form-item label="施工单位" prop="builder">
                                <el-input
                                    maxlength="50"
                                    v-model="addForm.builder"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                        <el-col :span="8"
                            ><el-form-item
                                label="施工类型"
                                prop="constructtype"
                            >
                                <el-select
                                    v-model="addForm.constructtype"
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in sglx"
                                        :key="item.DDID"
                                        :label="item.dataitem"
                                        :value="item.DDID"
                                    ></el-option> </el-select></el-form-item
                        ></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8"
                            ><el-form-item
                                label="工程造价(万元)"
                                prop="sitecost"
                            >
                                <el-input
                                    maxlength="50"
                                    v-model="addForm.sitecost"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                        <el-col :span="8"
                            ><el-form-item label="占地面积" prop="floorarea">
                                <el-input
                                    maxlength="50"
                                    v-model="addForm.floorarea"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                        <el-col :span="8"
                            ><el-form-item label="建筑面积" prop="buildingarea">
                                <el-input
                                    maxlength="50"
                                    v-model="addForm.buildingarea"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label="是否市管项目" prop="bcityctrl">
                                <el-select
                                    v-model="addForm.bcityctrl"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option key="1" label="是" value="1">
                                    </el-option>
                                    <el-option key="0" label="否" value="0">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8"
                            ><el-form-item label="是否推送到市里" prop="bpush">
                                <el-select
                                    v-model="addForm.bpush"
                                    filterable
                                    placeholder="请选择"
                                >
                                    <el-option key="1" label="是" value="1">
                                    </el-option>
                                    <el-option key="0" label="否" value="0">
                                    </el-option> </el-select></el-form-item
                        ></el-col>
                        <el-col :span="8"
                            ><el-form-item label="信用等级" prop="siterate">
                                <el-input
                                    maxlength="50"
                                    v-model="addForm.siterate"
                                    auto-complete="off"
                                ></el-input> </el-form-item
                        ></el-col>
                    </el-row>
                </el-form>
            </el-col>
            <el-col :span="6">
                <mark-map @callMap="callAddMap"></mark-map>
            </el-col>
        </el-row>

        <div style="text-align: center">
            <el-button @click.native="cancelAdd">返回</el-button>
            <el-button
                type="primary"
                @click.native="addSubmit"
                :loading="addLoading"
                >提交</el-button
            >
        </div>
    </section>
</template>
<script>
import { City, addSite } from "../../api/api";
import MarkMap from "../../components/MarkMap";
import dicmixin from "../../assets/dic/dicmixin";
import { mprovider } from "../../assets/dic/dic";
export default {
    components: { MarkMap },
    mixins: [dicmixin],
    data() {
        return {
            groups: [],
            sitetypes: [
                { text: "建筑工地", value: 172 },
                { text: "市政工程", value: 188 },
            ],
            citys: [],
            districts: [],
            addLoading: false,
            addFormRules: {
                groupname: [
                    {
                        required: true,
                        message: "请输入分组名称",
                        trigger: "blur",
                    },
                ],
                groupshortname: [
                    {
                        required: true,
                        message: "请输入分组简称",
                        trigger: "blur",
                    },
                ],
                city: [
                    {
                        required: true,
                        message: "请选择城市",
                        trigger: "blur",
                    },
                ],
                sitelng: [
                    {
                        required: true,
                        validator: (rules, value, callback) => {
                            let lat = this.addForm.sitelat;
                            if (!value || !lat) {
                                return callback(new Error("请填写经纬度"));
                            }
                            return callback();
                        },
                        trigger: "blur",
                    },
                ],
            },
            addForm: {},
            mprovider: mprovider,
            sglx: [],
            pickerOptionsStart: {
                disabledDate: (time) => {
                    if (this.addForm.enddate) {
                        return (
                            time.getTime() >
                            new Date(this.addForm.enddate).getTime()
                        );
                    }
                },
            },
            pickerOptionsEnd: {
                disabledDate: (time) => {
                    if (this.addForm.startdate) {
                        return (
                            time.getTime() <
                            new Date(this.addForm.startdate).getTime()
                        );
                    }
                },
            },
        };
    },
    methods: {
        changeCity(val) {
            let selG = this.groups.filter((g) => {
                return g.GROUPID == val;
            });
            if (selG && selG.length > 0) {
                this.addForm.sitecity = selG[0].city;
                this.districts = [];
                if (selG[0].city > 0) {
                    this.getAreas(selG[0].city, selG[0].district);
                }
            }
        },
        callAddMap(item) {
            this.$set(this.addForm, "sitelng", item.point.lng);
            this.$set(this.addForm, "sitelat", item.point.lat);
            this.$set(this.addForm, "siteaddr", item.address);
        },
        getCitys(pid) {
            let para = { pid: pid };
            City(para).then((res) => {
                if (res.success) {
                    this.citys = res.data;
                }
            });
        },
        getAreas(pid, selectedid) {
            let para = { pid: pid };
            City(para).then((res) => {
                if (res.success) {
                    this.districts = res.data;
                    if (selectedid) {
                        this.addForm.sitearea = selectedid;
                    } else {
                        this.addForm.sitearea = "";
                    }
                }
            });
        },
        cancelAdd() {
            this.$router.replace(`/site/sites`);
        },
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let para = Object.assign({}, this.addForm);
                    addSite(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.cancelAdd();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.addLoading = false;
                    });
                    //});
                }
            });
        },
    },
    mounted() {
        this.getGroups();
        this.getCitys(0);
        this.sglx = this.$store.state.options.sglx;
    },
};
</script>
<style scoped>
.bm-view {
    width: 100%;
    height: 600px;
}
.el-select,
.el-date-editor {
    width: 100%;
}
</style>


